import { message } from "antd";
import { AxiosRequestConfig } from "axios";
import { Config } from "../config";
import { LocalStorageKeys } from "../constants";
import { i18Get } from "../plugins/i18";

export const createLocation = (path: string, replacements: { [name: string]: string }): string => {
	for (const key in replacements) {
		path = path.replace(`:${key}`, replacements[key]);
	}
	return path;
};

export const validateUploadFile = (file: File): boolean => {
	if (!file) {
		return false;
	}
	if (file.size > 20971520) {
		message.error(i18Get("IMAGE_SIZE_TOO_LARGE", Config.defaultLanguage));
		return false;
	}
	if (!["image/jpeg", "image/jpg", "image/png"].includes(file.type)) {
		message.error(i18Get("UNSUPPORTED_FORMAT", Config.defaultLanguage));
		return false;
	}
	return true;
};

export const validatePemFile = (file: File): boolean => {
	if (!file) {
		return false;
	}
	if (!file.name.includes(".pem")) {
		message.error(i18Get("UNSUPPORTED_FORMAT", Config.defaultLanguage));
		return false;
	}
	return true;
};

export const showError = (msg: string): void => {
	message.error(i18Get(msg, Config.defaultLanguage));
};

export const showInfo = (msg: string): void => {
	message.info(i18Get(msg, Config.defaultLanguage));
};

export const showSuccess = (msg: string): void => {
	message.success(i18Get(msg, Config.defaultLanguage));
};

export const showWarning = (msg: string): void => {
	message.warning(i18Get(msg, Config.defaultLanguage));
};

export const axiosConfig = (): AxiosRequestConfig => ({
	headers: {
		"Accept-Language": Config.defaultLanguage,
		"Content-Type": "application/json",
	},
});

export const axiosMultiPartConfig = (): AxiosRequestConfig => ({
	headers: {
		"Authorization": `Bearer ${sessionStorage.getItem(LocalStorageKeys.TOKEN)}`,
		"Accept-Language": Config.defaultLanguage,
		"Content-Type": "multpart/form-data",
	},
});

