import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import { Config } from "../../config";
import { axiosConfig } from "../../utils/util.fns";

type ExportImagePayload = {
	image: string | ArrayBuffer | null;
	type: string;
};

export const exportText = createAsyncThunk("speech/text", async (payload: { prompt: string; lang: string }) => {
	const response = await axios.get(Config.apiUrl + `/text_to_text/?prompt=${payload.prompt}&lang=${payload.lang}`);
	return response.data;
});

export const exportSpeech = createAsyncThunk("speech/speech", async (payload: string) => {
	const response = await axios.post(Config.apiUrl + "/voice/", payload, axiosConfig());
	return response.data;
});

export const exportImage = createAsyncThunk("speech/image", async (payload: ExportImagePayload) => {
	const response = await axios.post(Config.apiUrl + `/${payload.type}/`, payload.image, axiosConfig());
	return response.data;
});
