import React, { useEffect, useState } from "react";
import { SpeechTranscript } from "../../components/speech-transcript";
import { RecordAudio } from "../../components/record-audio";
import "./speech.scss";
import { useNavigate } from "react-router-dom";
import { Locations } from "../../constants/locations";
import { useAppDispatch, useAppSelector } from "../../modal/hooks";
import { clearSpeechExport } from "../../services/speech/speech.slice";
import { APIStatus } from "../../constants";
import { exportSpeech } from "../../services/speech/speech.service";

export type SpeechTranscriptOutput = {
	text: string;
	audio?: string;
	edited: boolean;
};

export const Speech: React.FunctionComponent = () => {
	const navigate = useNavigate();
	const dispatch = useAppDispatch();
	const speechReducer = useAppSelector((store) => store.speech);
	const [data, setData] = useState({} as SpeechTranscriptOutput);
	const [speechStart, setSpeechStart] = useState(false);
	const [speechStop, setSpeechStop] = useState(false);
	const [loading, setLoading] = useState<boolean>(false);

	const uploadSpeech = () => {
		setLoading(true);
		const audioBase64 = JSON.stringify(JSON.stringify({ audiob64: data.audio, language: "Malayalam" }));
		dispatch(exportSpeech(audioBase64));
	};

	useEffect(() => {
		if (speechReducer.speechExportCompleteComplete === APIStatus.FULFILLED) {
			setLoading(false);
			navigate(Locations.RESULT);
			dispatch(clearSpeechExport());
		}
		if (speechReducer.speechExportCompleteComplete === APIStatus.REJECTED) {
			setLoading(false);
			dispatch(clearSpeechExport());
		}
	}, [speechReducer.speechExportCompleteComplete]);

	return (
		<>
			<div className="back_btn mb-3" onClick={() => navigate(Locations.DASHBOARD)}>
				<span onClick={() => navigate(Locations.DASHBOARD)}>
					<i className="fas fa-chevron-left"></i>
				</span>
			</div>
			<div className="speech_container">
				<div className="speech_value_container">
					<SpeechTranscript
						value={data}
						start={speechStart}
						stop={speechStop}
						onRecordCompleted={(e: SpeechTranscriptOutput) =>
							setData((prevState) => ({ ...prevState, text: e.text, edited: e.edited }))
						}
					/>
					<RecordAudio
						audio={data.audio}
						start={speechStart}
						stop={speechStop}
						onRecordCompleted={(e: string) => {
							setData((prevState) => ({ ...prevState, audio: e }));
							console.log("jjj", e);
						}}
					/>
				</div>
				<div className="custom_footer justify-content-end">
					{data.audio && !loading ? (
						<button onClick={() => setData({ text: "", edited: false, audio: undefined })} className="delete_btn">
							<div>
								<i className="fas fa-trash-alt delete_icon mb-2"></i>
							</div>
							Delete
						</button>
					) : (
						""
					)}
					{speechStart ? (
						<button
							className="primary_btn min_width_125"
							onClick={() => {
								setSpeechStart(false);
								setSpeechStop(true);
							}}
							disabled={loading}
						>
							<div>
								<i className="fas fa-microphone mb-2 stop_icon"></i>
							</div>
							Stop
						</button>
					) : (
						<button
							className="primary_btn min_width_125"
							onClick={() => {
								setData({ text: "", edited: false, audio: undefined });
								setSpeechStart(true);
								setSpeechStop(false);
							}}
							disabled={loading}
						>
							<div>
								<i className="fas fa-microphone mb-2 main_icon"></i>
							</div>
							{data.text || data.audio ? "Re Record" : "Start Record"}
						</button>
					)}
					<button disabled={!data.audio || loading} className="secondary_btn min_width_125" onClick={uploadSpeech}>
						<div>
							<i className="fas fa-paper-plane send_icon mb-2"></i>
						</div>
						Send
					</button>
				</div>
			</div>
		</>
	);
};
