import React from "react";
import { createBrowserRouter, Navigate } from "react-router-dom";
import { ErrorBoundary } from "../plugins/error-boundary";
import { Locations } from "../constants/locations";
import { MainContainer } from "../containers/main-container";
import { Speech } from "../containers/speech";
import { ImageBrowse } from "../components/image-browse";
import { Dashboard } from "../containers/dashboard";
import { Text } from "../containers/text";
import { Result } from "../containers/result";

export const webRouter = createBrowserRouter([
	{
		path: Locations.BASE,
		element: <MainContainer />,
		errorElement: <ErrorBoundary />,
		children: [
			{
				path: Locations.DASHBOARD,
				element: <Dashboard />,
				errorElement: <ErrorBoundary />,
			},
			{
				path: Locations.TEXT,
				element: <Text />,
				errorElement: <ErrorBoundary />,
			},
			{
				path: Locations.SPEECH,
				element: <Speech />,
				errorElement: <ErrorBoundary />,
			},
			{
				path: Locations.IMAGE_UPLOAD,
				element: <ImageBrowse />,
				errorElement: <ErrorBoundary />,
			},
			{
				path: Locations.RESULT,
				element: <Result />,
				errorElement: <ErrorBoundary />,
			},
		],
	},
	{
		path: "*",
		element: <Navigate to={Locations.BASE} />,
		errorElement: <ErrorBoundary />,
	},
]);
