import { createSlice } from "@reduxjs/toolkit";
import { APIStatus } from "../../constants";
import { exportImage, exportSpeech, exportText } from "./speech.service";

export interface SpeechReducer {
	textExportCompleteComplete: APIStatus.FULFILLED | APIStatus.REJECTED | null;
	textResponse: string;
	audioResponse: string | null;
	speechExportCompleteComplete: APIStatus.FULFILLED | APIStatus.REJECTED | null;
	imageExportCompleteComplete: APIStatus.FULFILLED | APIStatus.REJECTED | null;
}

const initialState: SpeechReducer = {
	textExportCompleteComplete: null,
	textResponse: "",
	speechExportCompleteComplete: null,
	imageExportCompleteComplete: null,
	audioResponse: null,
};

const speechSlice = createSlice({
	name: "speech",
	initialState,
	reducers: {
		clearTextExport(state) {
			state.textExportCompleteComplete = null;
		},
		clearSpeechExport(state) {
			state.speechExportCompleteComplete = null;
		},
		clearImageExport(state) {
			state.imageExportCompleteComplete = null;
		},
	},
	extraReducers(builder) {
		builder.addCase(exportText.fulfilled, (state, action) => {
			state.textExportCompleteComplete = APIStatus.FULFILLED;
			state.textResponse = action.payload.generated_text;
		});
		builder.addCase(exportText.rejected, (state) => {
			state.textExportCompleteComplete = APIStatus.REJECTED;
		});
		builder.addCase(exportSpeech.fulfilled, (state, action) => {
			state.speechExportCompleteComplete = APIStatus.FULFILLED;
			state.textResponse = action.payload.generated_text;
			state.audioResponse = action.payload.audiob64;
		});
		builder.addCase(exportSpeech.rejected, (state) => {
			state.speechExportCompleteComplete = APIStatus.REJECTED;
		});
		builder.addCase(exportImage.fulfilled, (state, action) => {
			state.imageExportCompleteComplete = APIStatus.FULFILLED;
			state.textResponse = action.payload.predicted_result;
		});
		builder.addCase(exportImage.rejected, (state) => {
			state.imageExportCompleteComplete = APIStatus.REJECTED;
		});
	},
});

export const { clearTextExport, clearSpeechExport, clearImageExport } = speechSlice.actions;

export default speechSlice.reducer;
