/* eslint-disable react/prop-types */
import React, { useEffect, useRef, useState } from "react";
import { showError } from "../../utils/util.fns";
import { useNavigate } from "react-router-dom";
import { Locations } from "../../constants/locations";
import { useAppDispatch, useAppSelector } from "../../modal/hooks";
import { APIStatus } from "../../constants";
import { clearImageExport } from "../../services/speech/speech.slice";
import { Modal } from "antd";

let streamData = null;

export const CaptureModal = (props) => {
	const navigate = useNavigate();
	const dispatch = useAppDispatch();
	const speechReducer = useAppSelector((store) => store.speech);
	const [captureComplete, setCaptureComplete] = useState(false);
	const [loading, setLoading] = useState(true);
	const [dimension, setDimension] = useState({ height: 2160, width: 4096 });
	const videoRef = useRef(null);
	const canvasRef = useRef(null);
	const imageRef = useRef(null);

	useEffect(() => {
		if (!window || !window.navigator || !window.navigator.mediaDevices) {
			showError("MEDIA_DEVICE_UNAVAILABLE");
		}
		if (!captureComplete) {
			window.navigator.mediaDevices
				.getUserMedia({
					video: { width: { ideal: 4096 }, height: { ideal: 2160 } },
					audio: false,
				})
				.then(function (stream) {
					streamData = stream;
					if (videoRef.current) {
						videoRef.current.srcObject = stream;
						videoRef.current.autoplay = false;
						videoRef.current.muted = true;
						videoRef.current.playsInline = true;
						setDimension((prevState) => ({
							...prevState,
							width: videoRef.current?.offsetWidth ?? 4096,
							height: videoRef.current?.offsetHeight ?? 2160,
						}));
						videoRef.current
							.play()
							.then(() => {
								setLoading(false);
								console.log("video started");
							})
							.catch((e) => {
								console.log("error", e);
							});
					}
				})
				.catch(function (error) {
					if (error.name === "ConstraintNotSatisfiedError") {
						showError("UNSUPPORTED_RESOLUTION");
					} else if (error.name === "NotAllowedError") {
						showError("PERMISSION_NOT_GRANTED");
					} else {
						showError(`getUserMedia error: ${error.name},  ${error}`);
					}
				});
		}

		return () => {
			streamData?.getTracks().forEach(function (track) {
				track.stop();
			});
		};
	}, [captureComplete]);

	const takePicture = () => {
		if (canvasRef.current) {
			const context = canvasRef.current.getContext("2d");
			canvasRef.current.width = dimension.width;
			canvasRef.current.height = dimension.height;
			if (videoRef.current) {
				context?.drawImage(videoRef.current, 0, 0, dimension.width, dimension.height);
				const data = canvasRef.current.toDataURL("image/png");
				imageRef.current?.setAttribute("src", data);
				if (data) {
					setCaptureComplete(true);
					streamData?.getTracks().forEach(function (track) {
						track.stop();
					});
				}
			}
		}
	};

	const clearPhoto = () => {
		setLoading(true);
		const context = canvasRef.current?.getContext("2d");
		if (context) {
			setCaptureComplete(false);
			context.fillStyle = "#AAA";
			context.fillRect(0, 0, dimension.width, dimension.height);
			const data = canvasRef.current ? canvasRef.current.toDataURL() : "";
			imageRef.current?.setAttribute("src", data);
		}
	};

	const fileUpload = () => {
		if (canvasRef.current) {
			const data = canvasRef.current.toDataURL("image/png");
			var blobBin = atob(data.split(",")[1]);
			var array = [];
			for (var i = 0; i < blobBin.length; i++) {
				array.push(blobBin.charCodeAt(i));
			}
			let file = new Blob([new Uint8Array(array)], { type: "image/png" });
			const reader = new FileReader();
			reader.onloadend = () => {
				const base64String = reader.result.replace("data:", "").replace(/^.+,/, "");
				props.onCaptureComplete(base64String);
			};
			reader.readAsDataURL(file);
		}
	};

	useEffect(() => {
		if (speechReducer.imageExportCompleteComplete === APIStatus.FULFILLED) {
			navigate(Locations.RESULT);
			dispatch(clearImageExport());
		}
		if (speechReducer.imageExportCompleteComplete === APIStatus.REJECTED) {
			dispatch(clearImageExport());
		}
	}, [speechReducer.imageExportCompleteComplete]);

	return (
		<Modal
			className="capture_modal"
			open
			onOk={() => props}
			footer={[
				<div key={"capture_footer"} className="custom_footer justify-content-end">
					{captureComplete ? (
						<>
							<button className="primary_btn min_width_120 mr-3" disabled={loading} onClick={clearPhoto}>
								<div>
									<i className="fas fa-images mb-2 main_icon"></i>
								</div>
								Re capture
							</button>
						</>
					) : (
						<button disabled={loading} className={"primary_btn"} onClick={() => (!loading ? takePicture() : null)}>
							<div>
								<i className="fas fa-images mb-2 main_icon"></i>
							</div>
							{loading ? "Loading..." : "Capture"}
						</button>
					)}
					{captureComplete ? (
						<>
							<button className="secondary_btn min_width_120" onClick={fileUpload}>
								<div>
									<i className="fas fa-paper-plane send_icon mb-2"></i>
								</div>
								Ok
							</button>
						</>
					) : (
						""
					)}
				</div>,
			]}
		>
			<div className="back_btn mb-3">
				<span onClick={() => props.setCaptureModal(false)}>
					<i className="fas fa-chevron-left"></i>
				</span>
			</div>
			<div className="upload_container">
				<div className="upload_mode_container">
					<div className="upload_mode_image_capture_box h-100">
						{!captureComplete ? (
							<div className="image_capture_container">
								<video ref={videoRef}>Video error</video>
							</div>
						) : null}
						<canvas hidden ref={canvasRef}></canvas>
						<div
							className={` ${
								captureComplete ? "image_capture_container d-flex align-items-center justify-content-center" : ""
							}`}
						>
							<img
								className="image_capture_container_img"
								ref={imageRef}
								hidden={!captureComplete}
								alt="The screen capture will appear in this box."
							/>
						</div>
					</div>
				</div>
			</div>
		</Modal>
	);
};
