/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
import React, { useEffect, useRef, useState } from "react";

export const SpeechTranscript = (props) => {
	let timer;
	const [recognition, setRecognition] = useState(null);
	const loadingRef = useRef(null);
	const [data, setData] = useState({ text: "", edited: false });

	useEffect(() => {
		window.SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
		setRecognition(new SpeechRecognition());

		return () => {
			if (recognition) {
				recognition.removeEventListener("end", recognition.start);
				recognition.removeEventListener("result", () => null);
			}
		};
	}, []);

	useEffect(() => {
		if ((navigator.mediaDevices.getUserMedia && window) || window.navigator || !window.navigator.mediaDevices) {
			if (recognition) {
				recognition.interimResults = true;
				recognition.lang = "ml_IN";
				recognition.addEventListener("result", (e) => {
					const transcript = Array.from(e.results)
						.map((result) => result[0])
						.map((result) => result.transcript)
						.join("");
					clearTimeout(timer);
					timer = setTimeout(() => {
						if (loadingRef.current) {
							loadingRef.current.style = "display: none";
						}
						setData((prevState) => ({ ...prevState, text: prevState.text + " " + transcript }));
					}, 1000);
					if (loadingRef.current) {
						loadingRef.current.style = "display: block";
					}
				});
			}
		} else {
			showError("Mic not available");
		}
	}, [recognition]);

	useEffect(() => {
		if (props.value) {
			setData({ text: props.value.text, edited: props.value.edited });
		}
	}, [props.value]);

	useEffect(() => {
		if (props.start) {
			startSpeech();
		}
	}, [props.start]);

	useEffect(() => {
		if (props.stop) {
			stopSpeech();
		}
	}, [props.stop]);

	const startSpeech = () => {
		if (!props.disabled) {
			recognition.start();
			recognition.addEventListener("end", recognition.start);
		}
	};

	const stopSpeech = () => {
		clearTimeout(timer);
		loadingRef.current.style = "display: none";
		recognition.removeEventListener("end", recognition.start);
		recognition.removeEventListener("result", () => null);
		recognition.stop();
		props.onRecordCompleted(data);
	};

	return (
		<>
			<textarea
				disabled={props.disabled}
				value={data.text}
				onChange={(e) => {
					props.onRecordCompleted({ text: e.target.value, edited: true });
					setData((prevState) => ({ ...prevState, text: e.target.value, edited: true }));
				}}
			/>
			<span style={{ display: "none" }} className="listening_loading" ref={loadingRef}>
				Listening...
			</span>
		</>
	);
};
