import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Locations } from "../../constants/locations";
import "./image-browse.scss";
import { exportImage } from "../../services/speech/speech.service";
import { APIStatus } from "../../constants";
import { clearImageExport } from "../../services/speech/speech.slice";
import { useAppDispatch, useAppSelector } from "../../modal/hooks";
import { CaptureModal } from "../capture-modal";

export const ImageBrowse = () => {
	const navigate = useNavigate();
	const dispatch = useAppDispatch();
	const speechReducer = useAppSelector((store) => store.speech);
	const [file, setFile] = useState(null);
	const [captureBase64, setCaptureBase64] = useState("");
	const [captureModal, setCaptureModal] = useState(false);
	const [activeTab, setActiveTab] = useState("plant_disease");
	const [loading, setLoading] = useState(false);

	const onFileChange = (value) => {
		if (value) {
			setFile(Array.from(value)[0]);
		}
	};

	const uploadData = () => {
		if (captureBase64) {
			setLoading(true);
			const imageB64 = JSON.stringify(JSON.stringify({ imageb64: captureBase64 }));
			dispatch(exportImage({ image: imageB64, type: activeTab }));
		} else if (file) {
			setLoading(true);
			const img = file;
			const reader = new FileReader();
			reader.onloadend = () => {
				const base64String = reader.result.replace("data:", "").replace(/^.+,/, "");
				const imageB64 = JSON.stringify(JSON.stringify({ imageb64: base64String }));
				dispatch(exportImage({ image: imageB64, type: activeTab }));
			};
			reader.readAsDataURL(img);
		}
	};

	useEffect(() => {
		if (speechReducer.imageExportCompleteComplete === APIStatus.FULFILLED) {
			setLoading(false);
			navigate(Locations.RESULT);
			dispatch(clearImageExport());
		}
		if (speechReducer.imageExportCompleteComplete === APIStatus.REJECTED) {
			setLoading(false);
			dispatch(clearImageExport());
		}
	}, [speechReducer.imageExportCompleteComplete]);

	return (
		<>
			<div className="back_btn mb-3">
				<span onClick={() => navigate(Locations.DASHBOARD)}>
					<i className="fas fa-chevron-left"></i>
				</span>
			</div>
			<div className="image_browse_tab_container">
				<ul>
					<li
						className={`${activeTab === "plant_disease" ? "active_tab" : ""}`}
						onClick={() => setActiveTab("plant_disease")}
					>
						<a>Plant Disease</a>
					</li>
					<li
						className={`${activeTab === "pest_detection" ? "active_tab" : ""}`}
						onClick={() => setActiveTab("pest_detection")}
					>
						<a>Pest Detection</a>
					</li>
					<li
						className={`${activeTab === "rice_disease_identify" ? "active_tab" : ""}`}
						onClick={() => setActiveTab("rice_disease_identify")}
					>
						<a>Rice Disease Identify</a>
					</li>
				</ul>
				<div className="upload_container">
					<div className="image_drag_container">
						{captureBase64 ? (
							<img src={`data:image/png;base64,${captureBase64}`} />
						) : !file ? (
							<>
								Click or Drag image here to upload
								<input
									className="import_input"
									type="file"
									accept=".png,.jpg,.jpeg"
									onChange={(e) => onFileChange(e.target.files)}
								/>
							</>
						) : (
							<div className="imported_item_container">
								<img src={window.URL.createObjectURL(file)} />
							</div>
						)}
					</div>
					<div className="custom_footer justify-content-end">
						{file && !loading ? (
							<button onClick={() => setFile(null)} className="delete_btn">
								<div>
									<i className="fas fa-trash-alt delete_icon mb-2"></i>
								</div>
								Delete
							</button>
						) : (
							""
						)}
						<button
							className="primary_btn min_width_125"
							onClick={() => {
								setCaptureModal(true);
							}}
							disabled={loading}
						>
							<div>
								<i className="fas fa-camera mb-2 main_icon"></i>
							</div>
							{loading ? "Loading..." : "Capture"}
						</button>
						<button disabled={(!file && !captureBase64) || loading} className="secondary_btn" onClick={uploadData}>
							<div>
								<i className="fas fa-paper-plane send_icon mb-2"></i>
							</div>
							Send
						</button>
					</div>
					{captureModal ? (
						<CaptureModal
							setCaptureModal={setCaptureModal}
							onCaptureComplete={(e) => {
								setCaptureBase64(e);
								setCaptureModal(false);
							}}
						/>
					) : (
						""
					)}
				</div>
			</div>
		</>
	);
};
