import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Locations } from "../../constants/locations";
import { useAppDispatch, useAppSelector } from "../../modal/hooks";
import { exportText } from "../../services/speech/speech.service";
import { APIStatus } from "../../constants";
import { clearTextExport } from "../../services/speech/speech.slice";

export const Text: React.FunctionComponent = () => {
	const dispatch = useAppDispatch();
	const navigate = useNavigate();
	const speechReducer = useAppSelector((store) => store.speech);
	const [data, setData] = useState<string | null>(null);
	const [invalid, setInvalid] = useState<boolean>(false);
	const [loading, setLoading] = useState(false);

	const uploadText = () => {
		if (!data) {
			setInvalid(true);
		} else {
			setLoading(true);
			dispatch(exportText({ prompt: data, lang: "Malayalam" }));
		}
	};

	useEffect(() => {
		if (speechReducer.textExportCompleteComplete === APIStatus.FULFILLED) {
			setLoading(false);
			navigate(Locations.RESULT);
			dispatch(clearTextExport());
		}
		if (speechReducer.textExportCompleteComplete === APIStatus.REJECTED) {
			setLoading(false);
			dispatch(clearTextExport());
		}
	}, [speechReducer.textExportCompleteComplete]);

	return (
		<div>
			<div className="back_btn mb-3" onClick={() => navigate(Locations.DASHBOARD)}>
				<span onClick={() => navigate(Locations.DASHBOARD)}>
					<i className="fas fa-chevron-left"></i>
				</span>
			</div>
			<div className="text_color mb-1">Enter issue</div>
			<div className="position-relative">
				{invalid ? <span className="invalid">Please Enter an issue</span> : ""}
				<textarea
					className="main_form_text_area"
					onChange={(e) => {
						setData(e.target.value);
						setInvalid(false);
					}}
				/>
				<div className="d-flex align-items-end justify-content-end mt-3">
					<button disabled={loading} className="secondary_btn" onClick={uploadText}>
						<div>
							<i className="fas fa-paper-plane send_icon mb-2"></i>
						</div>
						Send
					</button>
				</div>
			</div>
		</div>
	);
};
