import React from "react";
import { useAppSelector } from "../../modal/hooks";
import { useNavigate } from "react-router-dom";
import { Locations } from "../../constants/locations";

export const Result: React.FunctionComponent = () => {
	const speechReducer = useAppSelector((store) => store.speech);
	const navigate = useNavigate();

	return (
		<div className="main_dashboard p-3">
			<div className="back_btn mb-3" onClick={() => navigate(Locations.DASHBOARD)}>
				<span onClick={() => navigate(Locations.DASHBOARD)}>
					<i className="fas fa-chevron-left"></i>
				</span>
			</div>
			<div className="text_color mb-1">Your Result</div>
			<div className="position-relative">
				<textarea value={speechReducer.textResponse} className="main_form_text_area" disabled={true} />
			</div>
			{speechReducer.audioResponse ? (
				<div className="mt-3">
					<audio controls className="clip" src={`data:audio/webm;codecs=opus;base64,${speechReducer.audioResponse}`} />
				</div>
			) : null}
		</div>
	);
};
