/* eslint-disable react/prop-types */
import React, { useEffect, useRef } from "react";
import "./record.scss";

export const RecordAudio = (props) => {
	const record = useRef(null);
	const stop = useRef(null);
	const soundClips = useRef(null);
	const mainSection = useRef(null);

	useEffect(() => {
		if (props.start) {
			record.current.click();
		}
	}, [props.start]);

	useEffect(() => {
		if (props.stop) {
			stop.current.click();
		}
	}, [props.stop]);

	useEffect(() => {
		if ((navigator.mediaDevices.getUserMedia && window) || window.navigator || !window.navigator.mediaDevices) {
			const constraints = { audio: true };
			let chunks = [];

			const onSuccess = function (stream) {
				const mediaRecorder = new MediaRecorder(stream);
				record.current.onclick = function () {
					mediaRecorder.start();
					record.current.style.background = "red";
					stop.current.disabled = false;
					record.current.disabled = true;
				};
				stop.current.onclick = function () {
					mediaRecorder.stop();
					record.current.style.background = "";
					record.current.style.color = "";
					stop.current.disabled = true;
					record.current.disabled = false;
				};
				mediaRecorder.ondataavailable = function (e) {
					chunks.push(e.data);
					const reader = new FileReader();
					reader.onloadend = () => {
						const base64String = reader.result.replace("data:", "").replace(/^.+,/, "");
						console.log(base64String);
						props.onRecordCompleted(base64String);
					};
					reader.readAsDataURL(e.data);
				};
			};

			const onError = function (err) {
				console.log("The following error occured: " + err);
			};

			navigator.mediaDevices.getUserMedia(constraints).then(onSuccess, onError);
		} else {
			console.log("getUserMedia not supported on your browser!");
		}
	}, []);

	return (
		<div>
			<section ref={mainSection} className="main-controls">
				<div className="height_0" id="buttons">
					<button className="hidden_btn" ref={record}>
						Record
					</button>
					<button className="hidden_btn" ref={stop}>
						Stop
					</button>
				</div>
			</section>
			<section ref={soundClips} className="sound-clips">
				{props.audio ? <audio controls className="clip" src={props.audio} /> : ""}
			</section>
		</div>
	);
};
